html {
    font-size: 62.5%;
}

body {
    margin: 0px;;
    background-color: #14181B;
    font-family: 'VT323', monospace;
}

/* LAYOUT GITS*/

.flex {
    display: flex;
}

.space-between {
    justify-content: space-between;
}

.flex-end {
    justify-content: flex-end;
}

.flex-start {
    justify-content: flex-start;
}

.width--45 {
    width: 45%;
}

.width--100 {
    width: 100%;
}

/* COMPONENTS */

.app-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 100vh;
}

.button-container {
    display: flex;
    width: 33%;
}

.component-container {
    border: 2px solid #0F73B8;
    box-sizing: border-box;
    display:flex;
    flex-direction: column;
    width: 90%;
}

.heading-container {
    border: 2px solid #FDD931;
    box-sizing: border-box;
    width: 90%;
}

.management-button {
    background-color:#14181B;
    border: 2px solid #F24A43;
    box-sizing: border-box;
    color:#F24A43;
    font-family: 'VT323', monospace;
    font-size: 2.4rem;
    height: 40px;
    text-decoration: none;
    text-align: center;
}

.management-container {
    width: 90%;
}

.num-ops-button {
    background-color: #14181B;
    border: 2px solid #0F73B8;
    color: #0F73B8;
    font-family: 'VT323', monospace;
    font-size: 2.4rem;
    height: 40px;
    text-decoration: none;
    text-align: center;
    width: 40px;
}

.num-ops-container {
    align-items: center;
    display: flex;
    justify-content: space-around;
}

.value-container {
    width: 5.6rem;
}

/* TYPOGRAPHY */

.app-heading {
    color: #FDD931;
    font-size: 7.2rem;
    margin: 0;
    text-align: center;
}

.component-heading {
    border-bottom: 2px solid #0F73B8;
    color: #0F73B8;
    font-size: 3.2rem;
    margin: 0;
    text-align: center;
}

.component-value {
    color: #0F73B8;
    display: block;
    font-size: 7.2rem;
    text-align: center;
}